import { Subject, Observable } from 'rxjs';
import { EditorService, UndoService, CanvasService } from '../services';
import { EditorToolBase } from './tool.base';
import { takeUntil, throttleTime } from 'rxjs/operators';
import * as Hammer from 'hammerjs';
import * as paper from 'paper';
import { TOOLS } from '../enums/tools';

interface DrawPoint {
    x: number;
    y: number;
}

export abstract class EditorDrawToolBase extends EditorToolBase {
    protected abstract canvasService: CanvasService;
    protected abstract undoService: UndoService;
    protected abstract editorService: EditorService;
    protected abstract tool: TOOLS;
    protected abstract toolParams: any;

    hummerManager: HammerManager;
    isDrawing: boolean;
    drawPaths = [];
    drawPoints = [];
    centerPoint;
    drawStartPoint;

    enable() {
        super.enable();

        this.addSubscriptions();
    }

    disable() {
        super.disable();

        this.hummerManager.destroy();
    }

    addSubscriptions() {
        const pan = new Hammer.Pan();
        this.hummerManager = new Hammer.Manager(this.canvasService.getDrawElement());
        this.hummerManager.add([pan]);
        const pan$ = Observable.create((observer) => {
            this.hummerManager.on('pan', e => observer.next(e));
        }).pipe(throttleTime(30));


        this.hummerManager.on('panstart', (e) => {
            const point = this.canvasService.normalizePoint(e.center);
            this.drawStart(point);
        });
        pan$.pipe(takeUntil(this.clear$))
            .subscribe(e => {
                const point = this.canvasService.normalizePoint(e.center);
                this.draw(point);
            });
        this.hummerManager.on('panend', () => this.drawEnd());
    }

    drawStart({x, y}: DrawPoint) {
        this.drawPoints.push({x, y});
        const size = this.canvasService.getCanvasSize();
        this.isDrawing = true;
        this.centerPoint = this.getCanvasCenterPoint();
        const startPoint = this.createPoint(x, y);
        this.drawStartPoint = startPoint;
        const halfHeight = size.height / 2;
        const point2 = new paper.Point(y > halfHeight ? 0 : size.height, halfHeight);
        const vector1 = startPoint.subtract(this.centerPoint);
        const vector2 = point2.subtract(this.centerPoint);
        const angle = vector1.getAngle(vector2);
        this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
            this.drawStartIteration(zoneIndex, zoneAngle, zoneStep, startPoint, angle);
        });
    }

    abstract drawStartIteration(zoneIndex, zoneAngle, zoneStep, startPoint, angle);

    draw({x, y}: DrawPoint) {
        if (this.isDrawing) {
            this.drawPoints.push({x, y});

            const size = this.canvasService.getCanvasSize();
            const centerPoint = this.getCanvasCenterPoint();
            const startPoint = this.createPoint(x, y);

            const halfHeight = size.height / 2;
            const point2 = new paper.Point(y > halfHeight ? 0 : size.height, halfHeight);
            const vector1 = startPoint.subtract(centerPoint);
            const vector2 = point2.subtract(centerPoint);
            const angle = vector1.getAngle(vector2);
            this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
                this.drawIteration(zoneIndex, zoneAngle, zoneStep, startPoint, angle);
            });
            this.canvasService.drawProject.view.update();
        }
    }

    abstract drawIteration(zoneIndex, zoneAngle, zoneStep, startPoint, angle);

    drawEnd() {
        this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
            this.drawEndIteration(zoneIndex, zoneAngle, zoneStep);
        });
        this.canvasService.drawProject.view.update();
        this.isDrawing = false;

        const drawStep = {
            tool: {
                type: this.tool,
                params: this.toolParams
            },
            color: this.color,
            zonesCount: this.zonesCount,
            isDrawMirrored: this.isDrawMirrored,
            strokeWidth: this.strokeWidth,
            points: this.drawPoints
        };
        const drawPaths = this.drawPaths;
        const allDrawPaths = drawPaths.reduce((list, paths) => {
            paths.forEach(p => p && list.push(p));
            return list;
        }, []);
        const drawStepKey = this.editorService.addUserDrawStep(drawStep);
        this.canvasService.addPaperItems(drawStepKey, allDrawPaths);
        this.undoService.addCommand({
            undo: () => {
                this.editorService.removeUserDrawStep(drawStepKey);
                this.canvasService.removePaperItems(drawStepKey);
            },
            redo: () => {
                this.editorService.addUserDrawStep(drawStep, drawStepKey);
                this.canvasService.addPaperItems(drawStepKey, allDrawPaths);
            }
        });


        this.drawPaths = [];
        this.drawPoints = [];
    }

    abstract drawEndIteration(zoneIndex, zoneAngle, zoneStep);

    getCanvasCenterPoint() {
        const size = this.canvasService.getCanvasSize();
        return this.createPoint(size.width / 2, size.height / 2);
    }

    zonesIteration(cb: (zoneIndex: number, zoneAngle: number, zoneStep: number) => void) {
        const step = 360 / this.zonesCount;

        for (let i = 0; i < this.zonesCount; i++) {
            cb(i, step * i, step);
        }
    }

    createPoint(x: number, y: number): paper.Point {
        return new paper.Point(x, y);
    }
}
