import { Component, OnInit, ViewChild, ElementRef, OnDestroy, Input } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import iro from '@jaames/iro';
import { EditorService, UndoService, CanvasSize, IdbService, ToolService } from '../../services';
import { LayoutService } from '../../../common/services';
import { takeUntil, map, first } from 'rxjs/operators';


@Component({
  selector: 'app-mandala-editor',
  templateUrl: './mandala-editor.component.html',
  styleUrls: ['./mandala-editor.component.scss']
})
export class MandalaEditorComponent implements OnInit, OnDestroy {
    @Input() size: CanvasSize;
    @Input() fullScreen: boolean;

    fullScreenSize: CanvasSize;
    isVisibleTools = false;
    isLoaded = false;
    initState = null;
    isEnabledLoader$;

    get canvasSize() {
        return this.fullScreen && !this.size ? this.fullScreenSize : this.size;
    }

    constructor(
        private undoService: UndoService,
        private idbService: IdbService,
        private editorService: EditorService,
        private toolService: ToolService,
        private layoutService: LayoutService
    ) {}

    ngOnInit() {
        this.isEnabledLoader$ = this.layoutService.isEnabledLoader();

        this.undoService.enable();

        this.editorService.getSize().pipe().subscribe((size) => {
            if (!size) {
                this.isLoaded = false;
                this.size = null;
                setTimeout(() => this.initEditor(), 1000);
            }
        });
    }

    ngOnDestroy() {
        this.undoService.disable();
    }

    toggleTools() {
        this.isVisibleTools = !this.isVisibleTools;
    }

    initEditor() {
        this.isLoaded = false;

        this.fullScreenSize = {
            width: window.innerWidth,
            height: window.innerHeight,
        };

        this.idbService.getUserDrawState('current-state')
            .then((state) => {
                this.initState = state.steps;
                console.log('state size: ', state.size);
                if (state.size) {
                    this.size = state.size;
                } else {
                    state.size = this.canvasSize;
                    this.idbService.saveUserDrawState('current-state', state);
                }
                this.editorService.updateSize(this.canvasSize);
                this.isLoaded = true;
            });
    }
}
