import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import * as paper from 'paper';

export interface CanvasSize {
    width: number;
    height: number;
}

export interface CanvasPoint {
    x: number;
    y: number;
}

@Injectable()
export class CanvasService {
    private size: CanvasSize;
    private zonesElement: HTMLCanvasElement;
    private drawElement: HTMLCanvasElement;
    private fillElement: HTMLCanvasElement;
    private cachedCanvasRect: ClientRect;
    private cachedPaperGroups = new Map<string, any>();
    private cachedPaperRasterizedGroups = new Map<any, any>();
    fillProject: paper.Project;
    drawProject: paper.Project;

    clear() {
        this.size = null;
        this.zonesElement = null;
        this.drawElement = null;
        this.fillElement = null;
        this.cachedCanvasRect = null;
        this.cachedPaperGroups.clear();
        this.cachedPaperRasterizedGroups.clear();
        this.fillProject = null;
        this.drawProject = null;
    }

    setZonesElement(zonesElement: HTMLCanvasElement) {
        this.zonesElement = zonesElement;
    }

    setDrawElement(drawElement: HTMLCanvasElement) {
        this.drawElement = drawElement;
    }

    getDrawElement() {
        return this.drawElement;
    }

    setFillElement(fillElement: HTMLCanvasElement) {
        this.fillElement = fillElement;
    }

    getFillElement() {
        return this.fillElement;
    }

    getCanvasSize(): CanvasSize {
        return this.size;
    }

    updateCanvasesSize(size: CanvasSize) {
        this.size = size;
        const canvases = [this.zonesElement, this.drawElement, this.fillElement];
        canvases.forEach((canvas) => {
            if (canvas) {
                canvas.width = size.width;
                canvas.height = size.height;
            }
        });
    }

    getCanvasRect(useCachedCanvasRect?: boolean): ClientRect {
        if (useCachedCanvasRect && this.cachedCanvasRect) {
            return this.cachedCanvasRect;
        }

        this.cachedCanvasRect = this.drawElement.getBoundingClientRect();
        return this.cachedCanvasRect;
    }

    normalizePoint(point: CanvasPoint, useCachedCanvasRect?: boolean): CanvasPoint {
        const rect = this.getCanvasRect(useCachedCanvasRect);
        return {
            x: point.x - rect.left,
            y: point.y - rect.top
        };
    }

    addPaperItems(drawStepKey, items) {
        const group = new paper.Group(items);
        const rasterize = group.rasterize();
        rasterize.remove();
        group.remove();

        this.cachedPaperGroups.set(drawStepKey, group);
        this.cachedPaperRasterizedGroups.set(group, rasterize);

        this.fillProject.activeLayer.addChild(rasterize);
        this.fillProject.view.update();
        this.drawProject.view.update();
    }

    removePaperItems(drawStepKey) {
        const group = this.cachedPaperGroups.get(drawStepKey);
        const rasterize = this.cachedPaperRasterizedGroups.get(group);
        this.cachedPaperRasterizedGroups.delete(group);
        this.cachedPaperGroups.delete(drawStepKey);

        group.remove();
        rasterize.remove();
        this.fillProject.view.update();
        this.drawProject.view.update();
        console.log('this.fillProject: ', this.fillProject);
    }

    getCachedPaperGroups() {
        return this.cachedPaperGroups;
    }
}
