import { Component, OnInit, OnDestroy, Input, OnChanges, ViewChild, ElementRef } from '@angular/core';
import * as paper from 'paper';
import { EditorService, CanvasService, CanvasSize, IdbService, UndoService } from '../../../services';
import { Subject } from 'rxjs';
import { takeUntil, map, take, first, mergeMap } from 'rxjs/operators';
import { ToolService } from '../../../services/tool.service';
import { LayoutService } from 'src/app/modules/common/services';
import { EditorDrawToolBase } from '../../../tools/draw.base';
import { EditorTapToolBase } from '../../../tools/tap.base';


@Component({
  selector: 'app-mandala-editor-canvas',
  templateUrl: './mandala-editor-canvas.component.html',
  styleUrls: ['./mandala-editor-canvas.component.scss']
})
export class MandalaEditorCanvasComponent implements OnInit, OnDestroy {
    @Input() initState;

    @ViewChild('drawCanvas', {static: true}) drawCanvas: ElementRef;
    @ViewChild('fillCanvas', {static: true}) fillCanvas: ElementRef;
    @ViewChild('zonesCanvas', {static: true}) zonesCanvas: ElementRef;
    destroy$ = new Subject<boolean>();
    backgroundRect: paper.Path.Rectangle;
    tool: EditorTool;
    size: CanvasSize;

    get width() {
        return `${this.size.width}px`;
    }

    get height() {
        return `${this.size.height}px`;
    }

    constructor(
        private editorService: EditorService,
        private canvasService: CanvasService,
        private layoutService: LayoutService,
        private idbService: IdbService,
        private toolService: ToolService,
        private undoService: UndoService
    ) {}

    ngOnInit() {
        this.canvasService.setDrawElement(this.drawCanvas.nativeElement);
        this.canvasService.setFillElement(this.fillCanvas.nativeElement);
        this.canvasService.setZonesElement(this.zonesCanvas.nativeElement);

        this.editorService.getSize().subscribe(size => {
            if (size) {
                this.size = size;
                this.canvasService.updateCanvasesSize(this.size);
            }
        });

        this.initDraw();
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }

    initDraw() {
        const zonesProject = new paper.Project(this.zonesCanvas.nativeElement);
        zonesProject.view.autoUpdate = false;

        const fillProject = new paper.Project(this.fillCanvas.nativeElement);
        fillProject.view.autoUpdate = false;
        this.canvasService.fillProject = fillProject;

        const canvas: any = document.getElementById('canvas-draw');
        const drawProject = new paper.Project(canvas);
        drawProject.view.autoUpdate = false;
        this.canvasService.drawProject = drawProject;

        this.addBackgroundRect('white');
        drawProject.view.update();

        for (const key in this.initState) {
            if (this.initState.hasOwnProperty(key)) {
                const step = this.initState[key];
                this.editorService.setTool(step.tool);
                this.editorService.setColor(step.color);
                this.editorService.setStrokeWidth(step.strokeWidth);
                this.editorService.setZonesCount(step.zonesCount);
                if (step.isDrawMirrored) {
                    this.editorService.enableDrawMirrored();
                } else {
                    this.editorService.disableDrawMirrored();
                }

                this.editorService.getTool()
                    .pipe(
                        first(),
                        mergeMap(tool => this.toolService.createTool(tool, this.backgroundRect))
                    ).subscribe((tool) => {
                        if (this.tool) {
                            this.tool.disable();
                        }
                        this.tool = tool;
                        tool.enable();

                        step.points.forEach((point, index) => {
                            if (tool instanceof EditorDrawToolBase) {
                                if (index === 0) {
                                    tool.drawStart(point);
                                } else {
                                    tool.draw(point);
                                }
                            } else if (tool instanceof EditorTapToolBase) {
                                tool.tap(point);
                            }
                        });

                        if (tool instanceof EditorDrawToolBase) {
                            tool.drawEnd();
                        }
                    });
            }
        }

        this.editorService.inited();
        this.undoService.clear();

        this.editorService.getTool()
            .pipe(
                takeUntil(this.destroy$),
                mergeMap(tool => this.toolService.createTool(tool, this.backgroundRect))
            ).subscribe((tool) => {
                if (this.tool) {
                    this.tool.disable();
                }
                this.tool = tool;
                this.toolService.setCurrentTool(tool);
                tool.enable();
            });
    }

    addBackgroundRect(color) {
        const startRect = this.createPoint(0, 0);
        const rectSize = new paper.Size(this.size.width, this.size.height);
        const baseLayer = new paper.Layer();
        baseLayer.activate();
        this.backgroundRect = new paper.Path.Rectangle(startRect, rectSize);
        this.backgroundRect.fillColor = new paper.Color(color);

        const drawLayer = new paper.Layer();
        drawLayer.activate();
    }

    private createPoint(x: number, y: number): paper.Point {
        return new paper.Point(x, y);
    }
}
