import { EditorDrawToolBase } from './draw.base';
import { EditorService, UndoService, CanvasService } from '../services';
import * as paper from 'paper';
import { TOOLS } from '../enums/tools';

export class DrawImageTool extends EditorDrawToolBase {
    protected tool = TOOLS.DRAW_IMAGE;
    protected svgGroup: any;
    toolParams: any;
    scale;
    protected drawImage: paper.Raster;

    constructor(
        protected canvasService: CanvasService,
        protected editorService: EditorService,
        protected undoService: UndoService
    ) {
        super(editorService);
    }

    setDrawImage(svg, imagePath) {
        this.toolParams = { imagePath };
        this.svgGroup = paper.project.importSVG(svg, {insert: false});
        this.drawImage = this.svgGroup.rasterize();
        const isWidthBigger = this.drawImage.size.width > this.drawImage.size.height;
        const scale = 500 / (isWidthBigger ? this.drawImage.size.width : this.drawImage.size.height);
        this.drawImage.size = new paper.Size(
            scale * this.drawImage.size.width,
            scale * this.drawImage.size.height
        );
        // this.drawImage.scale(300 / (isWidthBigger ? this.drawImage.size.width : this.drawImage.size.height));
        console.log('this.drawImage: ', this.drawImage);
    }

    drawStartIteration(zoneIndex, zoneAngle, zoneStep, startPoint, angle) {
        const point = startPoint.rotate(zoneAngle, this.centerPoint);
        const path = this.drawImage.clone() as paper.Raster;
        path.position = point;
        path.scale(50 / path.size.width);

        paper.project.activeLayer.addChild(path);
        console.log('path: ', path);

        const diff = angle + zoneStep / 2;
        const mirrorPoint = point.rotate(diff * 2, this.centerPoint);
        const path2 = this.drawImage.clone();
        path2.position = mirrorPoint;

        if (this.isDrawMirrored) {
            paper.project.activeLayer.addChild(path2);
        }

        this.drawPaths.push([
            path,
            this.isDrawMirrored ? path2 : null
        ]);
    }

    drawIteration(zoneIndex, zoneAngle, zoneStep, startPoint, angle) {
        const dist = startPoint.getDistance(this.drawStartPoint);

        let [zonePath, mirrorPath] = this.drawPaths[zoneIndex];
        // paper.project.clear();
        if (zonePath) {
            zonePath.remove();

            const position = zonePath.position;
            zonePath = this.drawImage.clone();
            zonePath.position = position;
            this.scale = (50 + dist * 2) / zonePath.width;
            zonePath.scale(this.scale);
            const vector = startPoint.subtract(this.drawStartPoint);
            zonePath.rotate(vector.angle);
            paper.project.activeLayer.addChild(zonePath);
        }

        if (mirrorPath) {
            mirrorPath.remove();

            const position = mirrorPath.position;
            mirrorPath = this.drawImage.clone();
            mirrorPath.position = position;
            mirrorPath.scale((50 + dist * 2) / mirrorPath.width);
            const diff = angle + zoneStep / 2;
            const point = startPoint.rotate(zoneAngle, this.centerPoint);
            const mirrorPoint = point.rotate(diff * 2, this.centerPoint);
            const vector = mirrorPoint.subtract(mirrorPath.position);
            mirrorPath.rotate(vector.angle);
            paper.project.activeLayer.addChild(mirrorPath);
        }

        this.drawPaths[zoneIndex] = [zonePath, mirrorPath];


        // if (mirrorPath) {
        //     mirrorPath.scale(.1 * zoneIndex, .1 * zoneIndex);
        // }
    }

    drawEndIteration(zoneIndex, zoneAngle, zoneStep) {
        // let [zonePath, mirrorPath] = this.drawPaths[zoneIndex];
        // if (zonePath) {
        //     const drawImage = this.svgGroup.clone();
        //     drawImage.size = zonePath.size;
        //     drawImage.position = zonePath.position;
        //     drawImage.rotate(zonePath.rotation);
        //     // debugger;
        //     drawImage.scale(zonePath.scaling.x + zonePath.scaling.y);
        //     zonePath = drawImage;
        // }
        // if (mirrorPath) {
        //     const drawImage = this.svgGroup.clone();
        //     drawImage.size = mirrorPath.size;
        //     drawImage.position = mirrorPath.position;
        //     drawImage.scale = mirrorPath.scale;
        //     drawImage.rotate(mirrorPath.rotation);
        //     mirrorPath = drawImage;
        // }
        // this.drawPaths[zoneIndex] = [zonePath, mirrorPath];
    }
}
